.footer {
    margin-top: 10vh;
    width: 100%;
    background-color: var(--bg);
}

.footer__container {
    display: flex;
    justify-content: space-between;
    max-width: 80rem;
    margin-left: auto;
    margin-right: auto;
    padding: 20px;
    align-items: center;
}

.footer__logo {
    font-size: 30px;
    font-family: 900;
    color: white;
}

.footer__container p {
    font-size: 18px;
    cursor: pointer;
    color: white;
    padding: 10px 0;
}

@media(max-width: 850px) {
    .footer__container {
        flex-direction: column;
        text-align: center;
    }
    
}